import React from 'react'
import './App.css'
import { gql, useQuery } from '@apollo/client'
import loadingAnimation from './resources/loading.gif'
import Topics from './components/Topics'

const ALL_TOPICS = gql`
query {
  allTopics  {
    name
    id
  }
}
`

const App = () => {

  const result = useQuery(ALL_TOPICS)

  if (result.loading) {
    return (
      <div>
        <img src={loadingAnimation} alt='loading animation' />
        Loading...
      </div>
    )
  }

  return (
    <div className="App">
      <h4>Tietopiiri</h4>
      <Topics topics={result.data.allTopics} />
    </div>
  )
}

export default App
